import React,{useEffect, useState, useContext} from 'react'
import {Alert, Button, Modal, Form} from 'react-bootstrap';            
import { useHistory } from "react-router-dom";
import {ScoreContext} from '../App';

function StartPopup() {
    const scoreContext = useContext(ScoreContext);
    const [show, setShow] = useState(scoreContext.name==null);
    const [uname, setUname] = useState(null);
    let history = useHistory();

    function handleRules() {
        history.push("/");
    }

    const handleClose = () => {
        if(uname!=null && uname != ''){
        scoreContext.setName(uname)
        setShow(false);}
    }
    return (
        <div>
        {/* <Button variant="primary" onClick={handleShow}>
            Launch static backdrop modal
        </Button> */}
        <Modal
            show={show}
            backdrop="static"
            keyboard={false}>
            <Modal.Header>
            <Modal.Title>Welcome to Hangman</Modal.Title>
            </Modal.Header><Form>
            <Modal.Body>
            <Alert variant="info">
            
                
                <Form.Group controlId="formGridName">
                    <Form.Label>Name</Form.Label>
                    
                    <input type="text" class="form-control" id="name" placeholder="Enter your Name" name="name" onChange={e=>setUname(e.target.value)}/>
                </Form.Group>

                <Form.Group controlId="Theme">
                <Form.Label>Theme</Form.Label>
                <Form.Control as="select" defaultValue="Choose..." required>
                    <option>Choose...</option>
                    <option>Technology</option>
                    <option>Social Media</option>
                </Form.Control>
                </Form.Group>
                
            </Alert>   
            </Modal.Body>
            <Modal.Footer>
            <Button variant="outline-dark" onClick={handleRules}>
                Close
            </Button>
            <Button variant="outline-info" onClick={handleClose}> Play </Button>
            </Modal.Footer></Form>
        </Modal>
               
        </div>
    )
}

export default StartPopup
