import React, { useState, useEffect,useReducer } from 'react';
import Rules from './components/Rules';
import Play from './components/Play';
import { showNotification as show, checkWin } from './helpers/helpers';

import {Nav, Navbar} from 'react-bootstrap'
import { PrimeIcons } from 'primereact/api';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import './App.css';

export const ScoreContext = React.createContext();

const words = ['sruthi vellore'];
let selectedWord = words[Math.floor(Math.random() * words.length)];

function App() {
  const [playable, setPlayable] = useState(true);
  const [correctLetters, setCorrectLetters] = useState([]);
  const [wrongLetters, setWrongLetters] = useState([]);
  const [showNotification, setShowNotification] = useState(false);
  const [name, setName] = useState(null);

  const [resetTimer,setResetTimer]=useState(true);
  const [time,setTime] = useState(0);
  var doStart=0;
  const [nstart, setNstart]=useState(false);
  const [num, setNum]=useState(1);

  useEffect(()=>{
    if (selectedWord.indexOf(' ') !== -1) {
      setCorrectLetters([" "]);
    } 
  },[])

  useEffect(()=> {
    if(nstart){
      
      const timer = time < 10000 && resetTimer && setInterval(() => setTime(time+num), 1000);
      console.log(timer);
      console.log(num);
      return() => {
        console.log("Calling return..");
        clearInterval(timer)
        setResetTimer(true)
      };
    }
  },[time,resetTimer,nstart]);

  var initAlphabetState=[]
  for(var i =0;i<26;i++){
    initAlphabetState.push(true)
  }
  const [alphabetState, setAlphabetState]=useState(initAlphabetState);

  function updateAlphabet(value){
    var newKeyCode = value-65;
    var newState = alphabetState;
    newState[newKeyCode]=false;
    setAlphabetState(newState);
  }

  useEffect(() => {
    if(name == null)
      return
    const handleKeydown = event => {
      setNstart(true);
      const { key, keyCode } = event;
      if (playable && keyCode >= 65 && keyCode <= 90) {
        updateAlphabet(keyCode);
        const letter = key.toLowerCase();
        if (selectedWord.includes(letter)) {
          if (!correctLetters.includes(letter)) {
            setCorrectLetters(currentLetters => [...currentLetters, letter]);
            dispatch('increment');
          } else {
            show(setShowNotification);
          }
        } else {
          if (!wrongLetters.includes(letter)) {
            setWrongLetters(currentLetters => [...currentLetters, letter]);
            dispatch('decrement');
          } else {
            show(setShowNotification);
          }
        }
      }
    }
    window.addEventListener('keydown', handleKeydown);

    return () => window.removeEventListener('keydown', handleKeydown);
  }, [correctLetters, wrongLetters, playable, alphabetState,name]);

  function playAgain() {
    

    // Empty Arrays
    setPlayable(true);
    setCorrectLetters([]);
    setWrongLetters([]);
    setAlphabetState(initAlphabetState);
    setResetTimer(false);
    setTime(0);
    setNstart(false);
    setNum(1);

    const random = Math.floor(Math.random() * words.length);
    selectedWord = words[random];
    if (selectedWord.indexOf(' ') !== -1) {
      setCorrectLetters([" "]);
    } 
    
  }

  const [pscore,setPscore] = useState(0);
  const scoreInitial=0;
  const scoreReducer=(state,action)=>{
    setPscore(state)
      if(action==='increment')
        return(state+5);
      if(action==='decrement')
        return(state-3);
      else
        return(scoreInitial);
  }

  const [score,dispatch] = useReducer(scoreReducer,scoreInitial)

  return (
    <>
      <ScoreContext.Provider value={{score:score,pscore:pscore,time:time,setNum:setNum,name:name,setName:setName}}>
      <Navbar className='NavBar' bg="dark" expand="sm" variant="dark">
        <Navbar.Brand >Hangman Game</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="/"> <i className="pi pi-home"> Rules</i></Nav.Link>
            <Nav.Link href="/play">Play</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      
      <Router>
        <Switch>
        <Route path="/play">
          <Play state={alphabetState} wrongLetters={wrongLetters} selectedWord={selectedWord} correctLetters={correctLetters} setPlayable={setPlayable} playAgain={playAgain} showNotification={showNotification}/>
          </Route>
          <Route path="/">
            <Rules />
          </Route>
        </Switch>
    </Router>
    </ScoreContext.Provider>
    </>
  );
}

export default App;
